<template>
    <div class="header">
        <!-- SWITCHING TYPES OF UNTIS AS A NAV -->
        <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
            <div class="container-fluid">
                <a class="navbar-brand" href="#">Tempeture</a>
                <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDarkDropdown" aria-controls="navbarNavDarkDropdown" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
                </button>
                <div class="collapse navbar-collapse" id="navbarNavDarkDropdown">
                <ul class="navbar-nav">
                    <li class="nav-item dropdown">
                    <a class="nav-link dropdown-toggle" href="#" id="navbarDarkDropdownMenuLink" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                        Measuring Units
                    </a>
                    <ul class="dropdown-menu dropdown-menu-dark" aria-labelledby="navbarDarkDropdownMenuLink">
                        <li><a class="dropdown-item" href="#"  v-on:click="metric()">Fahrenheit</a></li>
                        <li><a class="dropdown-item" href="#" v-on:click="imperial()">Celsius</a></li>
                    </ul>
                    </li>
                </ul>
                </div>
            </div>
        </nav>



        <!--ERROR MESSAGE IF ZIP WRONG -->
        <div class="error" style="styleOfError">Invalaid Zip Code, Must real and contain 5 Charaters</div>

        <!-- ZIP CODE INPUT -->
        <div class="input-group mb-3">
            <input type="text" class="form-control" placeholder="Zip Code" aria-label="Zip Code" aria-describedby="button-addon2">
            <!-- onclick check for verification  -->
            <button  v-on:click="checkForm()" class="btn btn-outline-secondary" type="button" id="button-addon2">Check Your Weather</button>
        </div>

        <!--WEATHER UPDATE CARD  -->
        <!-- <div class="card" style="width: 18rem;"> -->
            <img v-bind:src="weatherimg" class="card-img-top" alt="Unavaiable">
            <div class="card-body">
                <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
            </div>
        <!-- </div> -->

       
        <!-- <select class="form-select" aria-label="Default select example">
            <option selected>Perferd Tempeture Units</option>
            <option value="1">C</option>
            <option value="2">F</option>
        </select> -->

              
    </div>
</template>

<script>
export default {
    name: "Header",
    props:['weatherdata','weatherimg','zip','units'],
    data(){
        return{
            zipError:"",
            styleOfError: "",
        }
    },
    methods:
    {
        //NAV BAR UNIT SWITCHING METHODES 
        imperial(){
            this.unit = 'imperial'
            this.$emit('unit','imperial')
        },
        metric(){
            this.unit = 'metric'
            this.$emit('unit','metric')
        },


        //THIS WILL CHECK THE ZIP TO SEE IF IT MATCHES THE FORMAT OF ZIPFORMAT
        checkForm(){
            if(this.zipFormat(this.zip) == true ){
                this.$emit('zip', this.zip);
               this.styleOfError = 'display: none';
          
            }else{
                this.styleOfError = 'display: inline-flex;  color: red';
            }
            
        },
        //THIS IS REGEX TO VALIDATE THE ZIP CODE 
        zipFormat(theValue){
            var validNum = /^\d{5}/;
            return validNum.test(theValue);
        }
    }
}
</script>

<style scoped>
    .header{
        background: rgb(181, 199, 223);
        color: rgb(22, 21, 41);
        text-align: center;
        padding: 10px;
        margin: 0px;
    }
    .error{
        color: red;
    }

</style>